import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { Camera } from '@ionic-native/camera';
import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import {  NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';
import { Connect } from '../../providers/connect';


@Component({
  selector: 'page-photos',
  templateUrl: 'photos.html',
})

export class PhotosPage {

  phlist : any;
  notes : any = "";
  addresses : any;
  mymode : any = 0;


  constructor(public navCtrl: NavController, public navParams: NavParams, private zone: NgZone, public alertCtrl: AlertController, public loadingCtrl: LoadingController, public cdr: ChangeDetectorRef, public camera : Camera, public connect: Connect, private transfer: FileTransfer) {
     this.phlist = [];
     this.addresses = [];
     this.notes = "";
     this.mymode = 0;
  }

  ionViewDidLoad() {
        let that=this;
        let url = 'getphotolist.php?rnd='+Math.random();
        this.connect.getList(url).subscribe(
         data => {
           that.zone.run(() => {
             that.addresses = data;
           });
         },
         err => {
            this.connect.logError(err)
         }
        ); 
  }

  takePic() { 

        this.camera.getPicture({quality: 80, targetWidth: 400, targetHeight : 400, cameraDirection: 1, correctOrientation: true, destinationType: 1, allowEdit:true}).then((imagePath) => {
           this.zone.run(()=> { this.uploadImage(imagePath); });

 	      }, (err) => {
 	      });
  }

  takePicold() { 
    this.zone.run(() => {
      let r = Math.floor(130 + Math.random() * 8);
      this.phlist.push({id : r, pic : 'http://theparcelpeople.com.au/catalogue/server/photos/p'+r+'.jpg'});
      console.log(this.phlist);
      this.cdr.markForCheck();
    });
  }

  sendCust() {
    this.mymode = 1;
  }

  sendIt(a) {
    let url = 'uploadpics.php?a='+a.email+'&photos='+JSON.stringify(this.phlist);
    console.log(url);
  }

  delPhoto(a) {
    let nw = [];
    this.zone.run(() => {
      for (var p in this.phlist) 
        if (this.phlist[p].id != a) 
          nw.push({ id : this.phlist[p].id, pic : 'http://theparcelpeople.com.au/catalogue/server/photos/p'+this.phlist[p].id+'.jpg'});
      this.phlist = nw;
      console.log(this.phlist);
      this.cdr.markForCheck();
    });
  }  

public uploadImage(imagePath) {
  // Destination URL
  var url = this.connect.getServerUrl() + 'upload.php';

  // File for Upload
  var targetPath = imagePath;

  // File name only
  var filename = imagePath.substr(imagePath.lastIndexOf('/') + 1);

  var options = {
    fileKey: "file",
    fileName: filename,
    chunkedMode: false,
    mimeType: "multipart/form-data",
    params : {}
  };
  
  const fileTransfer: FileTransferObject = this.transfer.create();


  let loader  = this.loadingCtrl.create({
    content: 'Uploading...',
  });
  loader.present();

  // Use the FileTransfer to upload the image
  fileTransfer.upload(url, targetPath, options).then(data => {
    alert(JSON.stringify(data));
    loader.dismiss()
  }, err => {
    loader.dismiss()
  });
}

}



